.showcase {
  background-image: url('/src/images/showcase-image.png');
  width: 100%;
  height: 200px;
  background-size: cover;
  background-position: center;

  @include on-tablet {
    height: 400px;
  }

  @include on-desktop {
    background-image: url('/src/images/showcase-on-desktop.png');
  }
}
